.top__phone-icons {
}
.top__phone {
  position: relative;
  /*margin-left: 107px;*/
  width: 112px;
  line-height: 12px;
  color: #ffffff;
  font-size: 11px;
  font-weight: 500;
}
.top__phone::before {
  content: "";
  position: absolute;
  top: 0;
  left: -20px;
  width: 10.5px;
  height: 10.5px;
  background-image: url(../../img/icons/img_phone.svg);

}
.top__addres-icons {
}
.top__addres {
  position: relative;
  /*margin-left: 51px;*/
  font-family: "Inter", sans-serif;
  font-size: 11px;
  color: #ffffff;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.03em;
}
.top__addres::before {
  content: "";
  position: absolute;
  top: 0;
  left: -20px;
  width: 9.8px;
  height: 11.8px;
  background-image: url(../../img/icons/img_addres.svg);
}

.top__workinghours-icons {
}
.top__workinghours {
  position: relative;
  /*margin-left: 75px;*/
  font-size: 11px;
  font-weight: 500;
  line-height: 12px;
  /*text-align: left;*/
